<template>
  <div class="xtx-carousel" @mouseenter="enter" @mouseleave="leave">
    <ul class="carousel-body">
      <li
        class="carousel-item"
        :class="{ fade: curIdx === idx }"
        v-for="(item, idx) in sliders"
        :key="idx"
      >
        <RouterLink to="/">
          <img :src="item.imgUrl" alt="" />
        </RouterLink>
      </li>
    </ul>
    <a href="javascript:;" @click="toggle(-1)" class="carousel-btn prev"
      ><i class="iconfont icon-angle-left"
    /></a>
    <a href="javascript:;" class="carousel-btn next" @click="toggle(1)"
      ><i class="iconfont icon-angle-right"
    /></a>
    <div class="carousel-indicator">
      <span
        v-for="(i, idx) in sliders.length"
        :key="i"
        @click="curIdx = idx"
        :class="{ active: idx === curIdx }"
      ></span>
    </div>
  </div>
</template>

<script>
import { onUnmounted, watch } from "@vue/runtime-core";
import { ref } from "vue";
export default {
  props: {
    sliders: {
      type: Array,
      default: () => [],
    },
    autoplay: {
      type: Boolean,
      default: false,
    },
    duration: {
      type: Number,
      default: 2000,
    },
  },
  name: "XtxCarousel",
  setup(props) {
    const curIdx = ref(0);

    //自动播放
    let timer = null;
    const autoplay = () => {
      timer = setInterval(() => {
        curIdx.value++;
        if (curIdx.value >= props.sliders.length) {
          curIdx.value = 0;
        }
      }, props.duration);
    };
    //sliders中有数据就自动播放
    watch(
      () => props.sliders,
      (newVal, oldVal) => {
        if (newVal.length > 0) {
          if (props.autoplay) {
            autoplay();
          }
        }
      },
      { immediate: true }
    );
    onUnmounted(() => {
      clearInterval(timer);
    });
    const leave = () => {
      if (props.autoplay) autoplay();
    };
    const enter = () => {
      clearInterval(timer);
    };
    const toggle = (step) => {
       curIdx.value += step;
       console.log(curIdx.value )
      if (curIdx.value >= props.sliders.length) {
        curIdx.value = 0;
      } else if (curIdx.value < 0) {
        curIdx.value = props.sliders.length - 1;
      } 
    };
    return {
      curIdx,
      leave,
      enter,
      toggle,
    };
  },
};
</script>

<style scoped lang="less">
.xtx-carousel {
  width: 100%;
  height: 100%;
  // min-width: 300px;
  // min-height: 150px;
  position: relative;
  .carousel {
    &-body {
      width: 100%;
      height: 100%;
    }
    &-item {
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;
      transition: opacity 0.5s linear;
      &.fade {
        opacity: 1;
        z-index: 1;
      }
      img {
        width: 100%;
        height: 100%;
      }
    }
    &-indicator {
      position: absolute;
      left: 0;
      bottom: 20px;
      z-index: 2;
      width: 100%;
      text-align: center;
      span {
        display: inline-block;
        width: 12px;
        height: 12px;
        background: rgba(0, 0, 0, 0.2);
        border-radius: 50%;
        cursor: pointer;
        ~ span {
          margin-left: 12px;
        }
        &.active {
          background: #fff;
        }
      }
    }
    &-btn {
      width: 44px;
      height: 44px;
      background: rgba(0, 0, 0, 0.2);
      color: #fff;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      z-index: 2;
      text-align: center;
      // background-color:red;
      line-height: 44px;
      opacity: 0;
      transition: all 0.5s;
      &.prev {
        left: 20px;
      }
      &.next {
        right: 20px;
      }
    }
  }
  &:hover {
    .carousel-btn {
      opacity: 1;
    }
  }
}
</style>