<template>
  <!-- <span>总数：{{ totalCount }}----双倍：{{ doubleCount }}</span> -->
  <div class="page-wrapper">
    <div>
      <span>count 点击次数: </span>
      <span>{{ count }}</span>
      <button @click="addCount">点击增加</button>
    </div>
    <div>
      <span>num 点击次数: </span>
      <span>{{ num }}</span>
      <button @click="addNum">点击增加</button>
    </div>
  </div>
  <!-- 使用teleport -->
  <button @click="openModal">Click to open modal! (With teleport!)</button>
  <teleport to="#endofbody">
    <div v-if="isModalOpen" class="modal">dasdasdasd</div>
  </teleport>
  <!-- <Suspense>
  <template >
    <Suspended-component />
  </template>
  <template #fallback>
    Loading...
  </template>
</Suspense> -->
</template>


<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import {
  computed,
  onBeforeMount,
  reactive,
  // readonly,
  ref,
  toRefs,
  watch,
  watchEffect,
} from "vue";
import { useRouter } from 'vue-router'
import {
  onMounted,
  onBeforeUpdate,
  onUpdated,
  onBeforeUnmount,
  onUnmounted,
  onRenderTracked, //状态跟踪
  onRenderTriggered, //状态触发
} from "vue";

export default {
  name: "Home",
  setup() {
    const router = useRouter()
    const state = reactive({
      //reactive 处理非基础类型数据的双向绑定，不可被解开，解开就失去双向绑定的功能
      count: 0,
      double: computed({
        get() {
          return state.count * 2;
        },
        set(newVal) {
          state.count = newVal / 2;
        },
      }),
      minObj: {
        innnerObj: {
          size: 0,
        },
      },
    });
    const num = ref(0); //ref处理基础类型的双向绑定
    // const readonlyState = readonly(state);
    // watch(
    //   () => readonlyState.count,
    //   () =>
    //     // newVal,oldVal
    //     {
    //       console.log("readonly state is changed");
    //       // setTimeout(()=>{
    //       //   readonlyState.count=666
    //       // },500)
    //     }
    // );
    const addCount = function () {
      state.count++;
    };
    const addNum = function () {
      console.log(num);
      router.push({
        path:'/about',
        query:{
          id:10
        }
      })
      num.value++;
      return num;
      // return 'fulkj'
    };
    const isModalOpen = ref(false);
    const openModal = function () {
      isModalOpen.value = !isModalOpen.value;
      console.log(isModalOpen);
    };
    //computed
    const totalCount = computed(() => state.count + num.value);
    // const doubleCount = computed({
    //   get() {
    //     return state.count * 2;
    //   },
    //   set(newVal) {
    //     state.count = newVal / 2;
    //   },
    // });

    //watch
    // 监听单个属性
    // watch(()=>totalCount.value,(newVal,oldVal)=>{
    //   console.log(`count+num=${newVal}`)
    // })
    //监听单个属性，immediate
    watch(
      () => totalCount.value,
      (newVal, oldVal) => {
        console.log(`count+num=${newVal},immediate=true`);
      },
      {
        immediate: true,
      }
    );
    //监听单个属性，deep
    watch(
      () => state.minObj,
      (newVal, oldVal) => {
        console.log(`state.minObj=${JSON.stringify(newVal)},deep=true`);
      },
      {
        deep: true,
      }
    );
    setTimeout(() => {
      state.minObj.innnerObj.size = 1;
    }, 2000);

    //监听多个属性 ref属性的数据直接用，reactive属性的数据需要()=>
    watch(
      [num, () => totalCount.value, () => state.count],
      ([numVal, totalVal, countVal], [oldNumVal, OldTotalVal, oldCount]) => {
        console.log(`num is ${numVal},count+num=${totalVal}`);
      }
    );
    watch(() => {
      console.log(">>>");
    });
    //副作用，会立即执行 但是可以停止监听 watchEffect 不需要指定监听的属性，他会自动的收
    //集依赖， 只要我们回调中引用到了 响应式的属性， 那么当这些属性变更的时候，这个回调都会执行
    //watchEffect返回一个停止监听的方法
    let callTimes = 0;
    const stopEffect = watchEffect(() => {
      console.log("watchEffective is called");
      const div = document.createElement("div");
      div.textContent = `totalCount is ${totalCount.value}`;
      document.body.appendChild(div);
      callTimes++;
      if (callTimes >= 5) stopEffect();
    });
    onBeforeMount(() => {
      console.log("component is onBeforeMount");
    });
    onMounted(() => {
      console.log("component is onMounted");
    });
    onBeforeUpdate(() => {
      console.log(router.currentRoute.value.matched);
    });
    onUpdated(() => {
      console.log("component is onUpdated");
    });
    onBeforeUnmount(() => {
      console.log("component is onBeforeUnmount");
    });
    onUnmounted(() => {
      console.log("component is onUnmounted");
    });
    onRenderTracked((event) => {
      console.log("状态跟踪组件----------->");
      console.log(event);
    });
    onRenderTriggered((event) => {
      console.log("状态触发组件--------------->");
      console.log(event);
    });
    return {
      ...toRefs(state), //toRefs函数可使reative类型解成ref类型的
      num,
      addCount,
      addNum,
      // min,
      isModalOpen,
      openModal,
    };
  },
  components: {
    // HelloWorld
  },
};
</script>
<style scoped>
.modal {
  width: 100px;
  height: 100px;
  background-color: red;
}
</style>
